import { ArrowBackIcon } from '@chakra-ui/icons';
import { Box, FormControl, IconButton, Input, Spinner, Text, useToast } from '@chakra-ui/react';
import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { getFullSender, getSender } from '../config/ChatLogics';
import { ChatState } from '../Context/ChatProvider'
import ProfileModal from './miscelenious/ProfileModal';
import UpdateGroupChatModal from './miscelenious/UpdateGroupChatModal';
import ScrollableChat from './ScrollableChat';
import './styles.css';
import io from 'socket.io-client'
import Lottie from 'react-lottie'
import animationData from '../animation/typing.json'

const ENDPOINT = "https://chat-always.herokuapp.com/";
var socket,selectedChatCompare

const SingleChat = ({fetchAgain,setFetchAgain}) => {
    const {user,selectedChat,setSelectedChat,notification,setNotification} = ChatState();
    const [messages,setMessages] = useState([]);
    const [loading,setLoading] = useState(false)
    const [newMessage,setNewMessage] = useState()
    const [socketConnected, setSocketConnected] = useState(false)
    const [typing,setTyping] = useState(false);
    const [isTyping,setIsTyping] = useState(false)

    const defaultOptions = {
        loop: true,
        autoplay: true,
        animationData: animationData,
        rendererSettings: {
          preserveAspectRatio: "xMidYMid slice",
        },
      };
      
    const toast = useToast();
    const fetchMessages = async ()=>{
        if(!selectedChat){
            return;
        }
        try {
            const config = {
                headers:{
                    Authorization:`Bearer ${user.token}`
                },
            }
            setLoading(true);
            const {data} = await axios.get(`/api/message/${selectedChat._id}`,config)
            // console.log(messages);
            setMessages(data)
            setLoading(false);
            socket.emit('join chat',selectedChat._id);
        } catch (error) {
            toast({
                title:'Error Occurred',
                description:'Failed to load messages',
                status:'error',
                duration:5000,
                isClosable:true,
                position:'bottom'
            })
            
        }
    }
   
    
    const sendMessage = async (event)=>{
        if(event.key==='Enter' && newMessage){
            socket.emit()
            try {
                const config = {
                    headers:{
                        'Content-type':'application/json',
                        Authorization:`Bearer ${user.token}`
                    },
                }
                setNewMessage("")
                const {data} = await axios.post('/api/message',{
                    content:newMessage,
                    chatId:selectedChat._id,
                },config)

                // console.log(data);

                
                socket.emit('new message',data);
                setMessages([...messages,data])
                             
            } catch (error) {
                toast({
                    title:'Error occurred',
                    description:'failed to send message',
                    status:'error',
                    duration:5000,
                    isClosable:true,
                    position:'bottom'
                })
            }
        }

    }
    useEffect(() => {
        socket = io(ENDPOINT);
        socket.emit('setup',user);
        socket.on('connected',() => setSocketConnected(true))
        socket.on('typing',()=>setIsTyping(true))
        socket.on('stop typing',()=>setIsTyping(false))
      
      }, [])

       
    useEffect(()=>{
        fetchMessages()
        selectedChatCompare = selectedChat;
    },[selectedChat])
    
    
    useEffect(() => {
        socket.on('messages received',(newMessageReceived)=>{
            if(!selectedChatCompare || selectedChatCompare._id !== newMessageReceived.chat._id){
                if(!notification.includes(newMessageReceived)){
                    setNotification([newMessageReceived,...notification])
                    setFetchAgain(!fetchAgain)
                }
            }else{
                setMessages([...messages,newMessageReceived])
            }
        })
       })
      
   
    console.log(notification,'-------------------');

    const typingHandler = (e)=>{
        setNewMessage(e.target.value);
     if (!socketConnected) return;
 
     if (!typing) {
       setTyping(true);
       socket.emit("typing", selectedChat._id);
     }
     let lastTypingTime = new Date().getTime();
     var timerLength = 3000;
     setTimeout(() => {
       var timeNow = new Date().getTime();
       var timeDiff = timeNow - lastTypingTime;
       if (timeDiff >= timerLength && typing) {
         socket.emit("stop typing", selectedChat._id);
         setTyping(false);
       }
     }, timerLength);
 
     }
    
   
    
  return (
    <>
        {selectedChat?(<>
            <Text
            fontSize={{base:'28px',md:'30px'}}
            pb={3}
            px={2}
            w='100%'
            fontFamily='Work sans'
            d='flex'
            justifyContent={{base:'space-between'}}
            alignItems='center'
            >
                <IconButton
                    d={{base:'flex',md:'none'}}
                    icon={<ArrowBackIcon/>}
                    onClick={()=> setSelectedChat("")}
                />
                {!selectedChat.isGroupChat?(
                    <>{getSender(user,selectedChat.users)}
                    <ProfileModal user={getFullSender(user,selectedChat.users)}/></>
                ):(<>
                    {selectedChat.chatName.toUpperCase()}
                    {<UpdateGroupChatModal
                        fetchAgain={fetchAgain}
                        setFetchAgain={setFetchAgain}
                    fetchMessages={fetchMessages}
                    />}
                </>)}
            </Text>
            <Box
            d='flex'
            flexDir='column'
            justifyContent='flex-end'
            p={3}
            bg='#E8E8E8'
            w='100%'
            h='100%'
            borderRadius='lg'
            overflowY='hidden'
            >
                {loading?(<Spinner
                    size='xl'
                    w={20}
                    h={20}
                    alignSelf='center'
                    margin='auto'
                />):(<div className='messages'>
                <ScrollableChat messages={messages}/>
                
                </div>)}
                <FormControl onKeyDown={sendMessage} isRequired mt={3}>
                {isTyping ? (<div>
                    <Lottie
                        width={70}
                        options={defaultOptions}
                        style={{ marginBottom: 15, marginLeft: 0 }}
                    />
                </div>
               
              ) : (
                <></>
              )}
                <Input
                    variant='filled'
                    bg='#E0E0E0'
                    placeholder='Enter the message'
                    onChange={typingHandler}
                    value={newMessage}
                />

                </FormControl>
            </Box>
        </>):(<Box
        d='flex'
        alignItems='center'
        justifyContent='center'
        h='100%'
        >
        <Text
        fontSize='3xl'
        pb={3}
        fontFamily='Work sans'
        >Click User To Start Chatting</Text>
            
        </Box>)}
    </>
  )
}

export default SingleChat